<template>
  <div class="all">
    <div class="pubhead">发布</div>
    <div class="pubtitle">
      <input type="text" v-model="title" placeholder="请输入标题" />
    </div>
    <div class="cate">
      <div style="margin-left: 10px">请选择你要发布的地方</div>
      <div class="choose">
        <select v-model="id">
          <option :value="jrtt">今日头条</option>
          <option :value="essc">二手市场</option>
          <option :value="hljy">婚恋交友</option>
          <option :value="zxzs">装修装饰</option>
          <option :value="mrtt">明日头条</option>
          <option :value="llsc">劳力市场</option>
        </select>
      </div>
    </div>
    <div class="pubcontent">
      <textarea
        style="word-break: break-all"
        v-model="content"
        placeholder="请输入内容"
      ></textarea>
    </div>
    <div class="tu">
      <!-- <van-uploader :after-read="afterRead" multiple  /> -->
      <van-uploader v-model="fileList" multiple />
    </div>
    <div class="btn">
      <van-button type="primary" @click="submit">确定</van-button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { Button, Notify, Toast, Uploader } from "vant";
import { nanoid } from "nanoid";
export default {
  name:'public',
  data() {
    return {
      title: "",
      content: "",
      fileList: [],
      id: "",
      jrtt: ["610128169b33ed00018ed5df", "今日头条"],
      essc: ["60e83b96827eca0001ba827c", "装修装饰"],
      hljy: ["609e99d6935e5b0001eaf4f2", "游戏专区"],
      zxzs: ["60e83b96827eca0001ba827c", "装修装饰"],
      mrtt: ["60e83bd9f6a3980001a11be5", "明日头条"],
      llsc: ["61863e3519452d00017f1a8c", "劳力市场"],
      a: 123,
    };
  },
  components: {
    [Button.name]: Button,
    [Notify.name]: Notify,
    [Uploader.name]: Uploader,
  },
  methods: {
    submit() {
      axios({
        method: "get",
        url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token",
      }).then((res) => {
        let fd = new FormData();
        fd.append("token", res.data.token);
        fd.append("file", this.fileList[0].file);
        fd.append("key", nanoid());
        axios({
          method: "post",
          url: "https://upload-z1.qiniup.com",
          data: fd,
        }).then((res) => {
          console.log("http://toutiao.longxiaokj.com/" + res.data.key);
          axios({
            method: "post",
            url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article",
            data: {
              title: this.title,
              content: this.content,
              cate_id: this.id[0],
              cate_name: this.id[1],
              author_id: localStorage.getItem("uid"),
              imageSrc: ["http://toutiao.longxiaokj.com/" + res.data.key],
            },
          }).then((res) => {
            if (!localStorage.getItem("token")) return Toast("请先登录");
            if (res.data.msg === "成功") {
              Notify({
                type: "primary",
                message: `发布成功`,
                duration: 1000,
              });
              this.$router.push("/index");
            } else {
              Notify({
                type: "warning",
                message: res.data.msg,
                duration: 1000,
              });
            }
          });
        });
      });
    },
  },
};
</script>

<style scoped>
.all {
  background-color: rgb(245, 247, 249);
  height: 620px;
  /* border: 1px solid red; */
}
.pubhead {
  /* border: 1px solid red; */
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: white;
  background-color: rgb(7, 193, 96);
}
.pubtitle {
  /* border: 1px solid red; */
  height: 40px;
  width: 99%;
  margin: 10px 0;
  overflow: hidden;
}
.pubtitle input {
  height: 100%;
  width: 99%;
  border: none;
  padding-left: 10px;
}
.cate {
  /* border: 1px solid red; */
  display: flex;
  justify-content: space-between;
  height: 50px;
  line-height: 50px;
  background-color: #fff;
  color: #666;
}
.choose {
  margin-right: 50px;
}
.choose select {
  /* border: 1px solid red; */
  height: 25px;
  width: 90px;
}
.pubcontent {
  /* border: 1px solid red; */
  height: 100px;
  margin: 10px 0;
}
.pubcontent textarea {
  width: 360px;
  border: none;
  overflow: visible;
  word-break: break-all;
  height: 50px;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 50px;
  padding: 10px 0px 50px 10px;
}
.tu {
  /* border: 1px solid red; */
  padding: 25px;
}
.tu svg {
  /* border:  1px solid red; */
  width: 30px;
  height: 30px;
}
.btn {
  /* border:  1px solid red; */
  text-align: center;
}
.btn button {
  width: 300px;
}
</style>